<template>
  <div class="pay">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow :fixed="true" @click-left="back" />

    <div class="address-box">
      <div class="address">
        <div class="address-content">
          <div class="clearfix">
            <div class="fl name">{{addressdata.name}}</div>
            <div class="fl phone">{{addressdata.tel}}</div>
            <div class="fr" @click="openAddressBox">
              <div class="fl arrow-box">
                <img src="../assets/images/dizhi.png" alt class="auto-img" />
              </div>
              <div class="fl address-title">选择地址</div>
            </div>
          </div>
          <div class="address-info clearfix">
            <div class="dingwei fl">
              <img src="../assets/images/dingwei.png" alt class="auto-img" />
            </div>
            <div class="fl">{{addressdata.address}}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="order-info">
      <div class="order-box">
        <div class="order-title">
          <van-divider :style="{ color: '#5f56ff', borderColor: '#7e7e7e'}">订单信息</van-divider>
        </div>

        <div class="cellboxb">
          <div class="clearfix cell-box" v-for="(item, index) in shopbagData" :key="index">
            <div class="fl pro-img">
              <img class="auto-img" :src="item.small_img" alt />
            </div>
            <div class="fl pro-info">
              <div class="text-box">
                <div class="clearfix">
                  <div class="fl text-name">{{item.name}}</div>
                  <div class="fl text-rule">{{item.rule}}</div>
                </div>
                <div class="text-enname">{{item.enname}}</div>
              </div>
              <div class="price-box">
                <div class="fl price">￥{{item.price}}</div>
                <div class="fr count">x{{item.count}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="pro-box">
          <div class="clearfix pro-boxing">
            <div class="fl pro-count">共计 {{product.count}} 件商品</div>
            <div class="fr pro-total">合计 ￥{{product.total}}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="pay-btn">
      <van-button round block color="#5f56ff" @click="pay">立即结算</van-button>
    </div>

    <van-popup v-model="isOpen" position="bottom" closeable>
      <div class="address-title-box">选择收货地址</div>
      <van-address-list
        v-model="addressaid"
        :list="list"
        default-tag-text="默认"
        @select="selectaddress"
        @add="newAddress"
      />
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/pay.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      //选择收货地址下边栏
      isOpen: false,
      //地址aid
      addressaid: "",
      //地址列表数据
      list: [],
      //选择地址数据
      addressdata: {
        name: "",
        tel: "",
        address: "",
      },
      //购物袋数据
      shopbagData: [],
      // 商品数据
      product: {
        count: 0,
        total: 0,
      },

      sids: [],
    };
  },

  created() {
    this.sids = this.$route.query.sids.split("-");
    // 查询收货地址
    this.getaddress();
    //根据sids查询购物袋数据
    this.getshopbagdata();
  },

  methods: {

    back() {
      this.$router.go(-1);
    },

    openAddressBox() {
      this.isOpen = true;
    },

    
    //查询收货地址
    getaddress() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 20000) {
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              this.list.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault),
              });

              if (Boolean(v.isDefault)) {
                this.addressaid = v.aid;
                this.addressdata.name = v.name;
                this.addressdata.tel = v.tel;
                this.addressdata.address = address;
              }
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

     //根据sids查询购物袋数据
    getshopbagdata() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 50000) {
            if (result.data.result.length === 0) {
              return this.$router.push({ name: "Menu" });
            }

            result.data.result.map((v) => {
              this.product.count += v.count;
              this.product.total += v.count * v.price;
            });

            this.shopbagData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //选择地址
    selectaddress(item) {
      // 
      for (let key in this.addressdata) {
        this.addressdata[key] = item[key];
      }
    },

    //新增地址
    newAddress() {
      this.$router.push({ name: "NewAddress" });
    },


   

    //立即结算
    pay() {

      //判断是否选择地址
      if (this.addressaid == "") {
        this.$toast("请选择收货地址");
        return;
      }

      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(this.sids),
        phone: this.addressdata.tel,
        address: this.addressdata.address,
        receiver: this.addressdata.name,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/pay",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.$toast(result.data.msg);
          if (result.data.code == 60000) {
            this.$router.push({ name: "Order" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>
